.products__card {
    font-style: normal;
  text-align: center;
  color: $hero-uptitle-color;
  width: 100%;
  height: 100%;
  @include mq(mobile-only) {
    padding-top: 160px;
    padding-bottom: 40px;
  }

  @include mq(tablet) {
    padding-top: 118px;
    padding-bottom: 40px;
  }
  @include mq(desktop) {
    padding-top: 165px;
    padding-bottom: 60px;
  }
}
.products__card-header {
  font-family: 'Titan One';
  font-weight: 400;
  text-transform: uppercase;
  @include fonts(20px, 1.15, 0.04em);
  @include mq(desktop) {
    @include fonts(30px, 1.13, 0.04em);
  }
}
.products__card-header::after {
  display: block;
  content: '...';
  margin-top: 32px;
}
.products__card-text {
  position: relative;
  font-weight: 700;
  @include fonts(14px, 1.52, 0.04em);
  margin-top: 32px;
  margin-bottom: 32px;
  @include mq(tablet) {
    line-height: 1.76;
  }
  @include mq(desktop) {
    font-size: 16px;
    margin-bottom: 60px;
  }
}
.products__btn {
  cursor: url(../images/cursor-active1.png), default;
  min-width: 40px;
  min-height: 40px;
  border-radius: 50%;
  border: none;
  fill: $ligth-mode-btn;
}



